<template>
  <div class="frame-container"  style="background:#fff">
    <div class="vm-page-head-fixed vm-height-83px">
      <div class="vm-page-head--body vm-height-83px">
        <div class="vm-page-head--body-title">
          <!-- <div class="client-info__padding"></div> -->
          <!--左侧-->
          <div>
            <span class="icon iconfont iconsearch" style="opacity: 0; display:none;"></span>
          </div>
          <!--中间-->
          <div class="vm-tabs">
            <div>
              <div class="vm-tab-btns">
                <div
                  class="vm-tab-btn"
                  v-for="(item,i) in btnList"
                  :key="`btnList_${i}`"
                  :class="active===item.name?'active': ''"
                  @click="btnTabClick(item)"
                >{{item.aliasName || item.name}}</div>
                
              </div>
            </div>
          </div>
          <!--右侧-->
          <el-button
            class="header-new"
            @click="addAgencyClick"
            type="primary"
            size="mini"
            style="font-size:14px"
          >新增</el-button>
          <!-- <span class="header-new"
                style="height:40px;
                      border:1px solid #4ba8ff;
                      width:40px;
                      border-radius:5px;
                      display: flex;
                      justify-content: center;
                      align-items: center;
                      font-size: 30px;
                      font-weight:bold;
                      background-color: #4BA8FF"
                @click="addAgencyClick">+
          </span>-->
        </div>
        <FzyTabs :active="active" :tabList="tabList" @change="changeAgentTab"></FzyTabs>
      </div>
    </div>
    <mu-container ref="container" class="demo-loadmore-content">
      <mu-load-more
        ref="container"
        @refresh="refresh"
        @load="load"
        :refreshing="refreshing"
        :loading="loading"
        :loaded-all="loadedAll"
      >
        <div class="vm-height-10px vm-bg-gray"></div>
        <AgencyList v-if="active==='一级代理'||active==='二级代理'||active==='三级代理'" :list="agetList"></AgencyList>
        <ClientList v-if="active==='商户'" :list="clientList"></ClientList>
        <FzyNoData v-if="(active==='一级代理'||active==='二级代理' || active==='三级代理') && !agetList.length"></FzyNoData>
        <FzyNoData v-else-if="active==='商户' && !clientList.length"></FzyNoData>
        <div class="vm-height-49px"></div>
      </mu-load-more>
    </mu-container>
  </div>
</template>

<script>
import { agentOrClient } from "@/api";
import * as types from "@/router/types";
import FzyTabs from "@/components/common/fzyTabs";
import FzyNoData from "@/components/common/fzyNoData";
import AgencyList from "./components/agencyList";
import ClientList from "./components/clientList";
import { afterLoginInfoLocal } from "@/storage";

export default {
  // mixins: [isShowFooterMixin],
  components: { AgencyList, ClientList, FzyTabs, FzyNoData },
  data() {
    return {
      active: "一级代理",
      tabList: [
        { name: "全部", value: "" },
        { name: "未签约", value: 1 },
        { name: "待审核", value: 2 },
        { name: "已签约", value: 3 },
        // { name: '驳回', value: 6 },
        { name: "禁用", value: 5 },
        { name: "过期", value: 4 }
        // { name: '未启用', value: 1 },
      ],
      agentTabList: [
        { name: "全部", value: "" },
        { name: "未签约", value: 1 },
        { name: "待审核", value: 2 },
        { name: "已签约", value: 3 },
        { name: "驳回", value: 6 },
        { name: "禁用", value: 5 },
        { name: "过期", value: 4 }
      ],
      clientTabList: [
        { name: "全部", value: "" },
        { name: "已签约", value: 1 },
        { name: "未签约", value: 2 }
      ],
      btnList: [
        { name: "代理", color: "primary", textColor: "white" },
        { name: "商户", color: "white", textColor: "primary" }
      ],
      refreshing: false,
      loading: false,
      loadedAll: false,
      // 代理商
      agentParams: {
        pageNumber: 1,
        pageSize: 10,
        status: ""
      },
      agentTotal: 0,
      agetList: [],
      // 商户
      clientParams: {
        pageNumber: 1,
        pageSize: 20,
        companyId: afterLoginInfoLocal.getJSON().companyId,
        userType: afterLoginInfoLocal.getJSON().userType
      },
      clientTotal: 0,
      clientList: [],
      levelAlias: {
        oemName: "",
        firstName: "",
        secondName: "",
        thirdName: ""
      }
    };
  },
  watch: {
    active(val) {
      if (val === "一级代理" || val === "商户") {
        this.tabList = this.tabList.filter(res => res.name !== "驳回");
      } else {
        let flag = this.tabList.every(res => res.name !== "驳回");
        if (flag) {
          let obj = { name: "驳回", value: 6 };
          this.tabList.splice(4, 0, obj);
        }
      }
    }
  },
  created() {
    this.levelAlias = afterLoginInfoLocal.getJSON().levelAlias;
    console.log(this.levelAlias, "this.levelAlias");
    // this.getAgentList()
    if (this.clientParams.userType === 1) {
      // 服务商
      this.active = "一级代理";
      this.btnList = [
        {
          name: "一级代理",
          color: "primary",
          textColor: "white",
          aliasName: this.levelAlias.firstName
        },
        {
          name: "二级代理",
          color: "white",
          textColor: "primary",
          aliasName: this.levelAlias.secondName
        },
        {
          name: "三级代理",
          color: "white",
          textColor: "primary",
          aliasName: this.levelAlias.thirdName
        },
        { name: "商户", color: "white", textColor: "primary" }
      ];
    } else if (this.clientParams.userType === 2) {
      // 一级代理商
      this.active = "二级代理";
      this.btnList = [
        {
          name: "二级代理",
          color: "primary",
          textColor: "white",
          aliasName: this.levelAlias.secondName
        },
        {
          name: "三级代理",
          color: "white",
          textColor: "primary",
          aliasName: this.levelAlias.thirdName
        },
        { name: "商户", color: "white", textColor: "primary" }
      ];
    } else if (this.clientParams.userType === 3) {
      // 二级代理商
      this.active = "三级代理";
      this.btnList = [
        {
          name: "三级代理",
          color: "primary",
          textColor: "white",
          aliasName: this.levelAlias.thirdName
        },
        { name: "商户", color: "white", textColor: "primary" }
      ];
    } else if (this.clientParams.userType === 4) {
      // 三级代理商
      this.active = "商户";
      this.btnList = [{ name: "商户", color: "primary", textColor: "white" }];
      this.getClientList();
    }
    this.getListApi();
  },
  methods: {
    /**
     * 获取代理商列表
     */
    getListApi() {
      var getApi = "";
      if (this.active === "一级代理") {
        getApi = agentOrClient.getOneAgentList(this.agentParams);
      } else if (this.active === "二级代理") {
        getApi = agentOrClient.getAgentList(this.agentParams);
      } else if (this.active === "三级代理") {
        getApi = agentOrClient.getTreeAgentList(this.agentParams);
      }
      if (!getApi) return;
      getApi.then(res => {
        this.agentTotal = res.obj.totalElements;
        this.agetList = this.agetList.concat(res.obj.content);
        if (this.agetList.length < this.agentTotal) {
          this.loadedAll = false;
        } else {
          this.loadedAll = true;
        }
        // 分页组件
        this.refreshingStatus(false);
        this.loadingStatus(false);
      });

      // agentOrClient.getAgentList(this.agentParams).then(res => {
      //   this.agentTotal = res.obj.totalElements
      //   this.agetList = this.agetList.concat(res.obj.content)
      //   if (this.agetList.length < this.agentTotal) {
      //     this.loadedAll = false
      //   } else {
      //     this.loadedAll = true
      //   }
      //   // 分页组件
      //   this.refreshingStatus(false)
      //   this.loadingStatus(false)
      // })
    },
    /**
     * 获取商户列表
     */
    getClientList() {
      this.refreshingStatus(false);
      let companyId = afterLoginInfoLocal.getJSON().companyId;
      let params = {
        pageNumber: this.clientParams.pageNumber,
        pageSize: this.clientParams.pageSize,
        status: this.clientParams.status,
        companyId: companyId
      };
      agentOrClient.getClientListNew(params).then(res => {
        this.clientTotal = res.obj.totalElements;
        this.clientList = this.clientList.concat(res.obj.content);
        if (this.clientList.length < this.clientTotal) {
          this.loadedAll = false;
        } else {
          this.loadedAll = true;
        }
        // 分页组件
        this.refreshingStatus(false);
        this.loadingStatus(false);
      });
    },
    /**
     * 刷新处理
     */
    refreshingStatus(val) {
      this.refreshing = val;
    },
    /**
     * 刷新处理--加载
     */
    loadingStatus(val) {
      this.loading = val;
    },
    btnTabClick(item) {
      if (this.active === item.name) {
        return;
      }
      this.btnList.forEach(o => {
        o.color = "white";
        o.textColor = "primary";
      });
      item.color = "primary";
      item.textColor = "white";
      this.active = item.name;
      if (this.active.indexOf("代理") !== -1) {
        console.log(1)
        this.agentParams.status = "";
        this.tabList = this.agentTabList;
      } else {
        console.log(2)
        this.clientParams.status = "";
        this.tabList = this.clientTabList;
      }
      this.refresh();
    },
    refresh() {
      this.$refs.container.scrollTop = 0;
      this.loadedAll = false;
      if (this.active.indexOf("代理") !== -1) {
        this.agentParams.pageNumber = 1;
        this.agetList = [];
        this.agentTotal = 0;
        this.getListApi();
      } else {
        this.clientParams.pageNumber = 1;
        this.clientList = [];
        this.clientTotal = 0;
        this.getClientList();
      }
      // this.refreshingStatus(true)
      this.$refs.container.scrollTop = 0;
    },
    load() {
      this.loadingStatus(true);
      if (this.active.indexOf("代理") !== -1) {
        this.agentParams.pageNumber += 1;
        this.getListApi();
      } else {
        console.log("加载更多");
        this.clientParams.pageNumber += 1;
        this.getClientList();
      }
    },
    changeAgentTab(val) {
      this.agentParams.status = val;
      this.clientParams.status = val;
      this.refresh();
    },
    /**
     * 新增代理商
     */
    addAgencyClick() {
      if (this.active.indexOf("代理") !== -1) {
        this.$router.push({
          name: types.AGENCY_EDIT,
          query: { name: this.active }
        });
      } else {
        this.$router.push({ name: types.CLIENT_EDIT });
      }
    }
  }
};
</script>



